@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,100&display=swap');
:root {
  --red: #474306;
  --bgColor: bisque;
}
body{
  background-color: #FBF8CC;
}
.nick h1{
  color: #03045E;
  padding: 15px;
}
.text1 a{
  color: #03045E;
  font-weight: 400;
  padding: 15px;
  text-decoration: none;
}
nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.text1{
  display: flex;
}
.icons img{
  padding: 10px;
}
.me{
  padding: 40px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.me h4{
  color: #03045E;
  font-size: large;
  margin: 0;
}
.me h1{
  margin: 0;
  font-size: 90px;
  color: #03045E;
  font-weight: 800;
}
.resume{
  margin-top: 20px;
  width: 150px;
  height: 50px;
  font-size: 15px;
  background-color: #F5EE84;
  border-radius: 10px;
  border: 0.5px solid black;
  cursor: pointer;
}

.photo img{
  box-shadow:
  -50px -70px 0 -40px var(--red),
  50px 70px 0 -40px var(--red);
}
.about{
  padding: 100px;
}
.h5{
  color: #F7F197;
  font-size: 70px;
  margin: 0;
  padding: 10px 10px 50px;
}
.about p{
  margin: 0;
  padding: 5px;
  font-size: 20px;
  font-weight: 400;
  width: 100%;
  height: 100%;
}
.nuqta{
  display: flex;
  align-items: center;
  width: 10px;
  height: 10px;
  background-color: #03045E;
  border-radius: 50%;
}
.bolim1 h3{
  margin: 0;
  padding: 10px;
  color: #03045E;
}
.year{
  display: flex;
  align-items: center;
}
.bolim1{
  justify-content: center;
  margin-top: 50px;
  display: flex;
  align-items: baseline;
}
.bolim1 p{
  margin: 0;
  font-size: 20px;
  font-weight: 400;
  color: #03045E;
}
.lorem{
  color: #03045E;
  font-size: 20px;
  font-weight: 400;
}
.page1 h3{
  font-size: 25px;
  color: #03045E;
  font-weight: 700;
}
.nov{
  color: #03045E;
  font-family: 'Poppins', sans-serif;
  font-weight: 900;
}
.study{
  display: flex;
  justify-content: space-around;
}
.contact{
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 50px;
}
.con{
  color: #F7F197;
  font-size: 70px;
  margin: 0;
  padding: 70px;
}
.contact p{
  margin: 0;
  padding: 5px;
  font-size: 20px;
  font-weight: 400;
  color: #03045E;
}
.pp{
  padding-top: 70px !important;
}
@media (max-width: 800px){
  body{
    width: 60%;
  }
  nav{
    margin-left: 180px;
    width: 80%;
  }
  .me{
    display: grid;
    justify-content: center;
    margin-left: 200px;
    width: 80%;
  }
  .photo{
    margin-top: 50px;
  }
  .about{
    padding: 0 100px;
    text-align: center;
    width: 100%;
  }
  .bolim1{
    padding: 0 200px;

    font-size: 20px;
  }
  .study{
    display: grid;
    padding: 30px 300px;
  }
  .contact{
    display: grid;
    margin-left: 250px;
  }
}